.login-container {
    max-width: 420px;
    margin: 0 auto;
    margin: 0 auto;
    margin-top: 40px;
}

.login-form-button {
    width: 100%;
}
.nav.navbar, .navbar-default {
    background-color: #f0f1f2;
    background-image: none;
    border: none;
    border-radius: 0;
    background: transparent;
}
  
.navbar-default .navbar-brand {
    color: #3498db;
    font-family: "Ubuntu";
    font-size: 21px;
}
  


.navbar-default .navbar-nav > li > a:hover {
    color: #3498db;
    text-decoration: none;
    border-bottom: 2px solid #3498db;
}
  
.div-votes {
    font-family: georgia;
    font-size: medium;
    display: inline-block;
    border-style: none;
}

.title {
    padding-left: 5px;
    padding-right: 5px;
    font-family: georgia;
    font-size: medium;
}

.vote {
    font-family: georgia;
    font-size: medium;
    padding-left: 5px;
    padding-right: 15px;
}

